import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {QaissuePage} from '../qaissue/qaissue';
import {FnissuePage} from '../fnissue/fnissue';
import {TranslateService} from '@ngx-translate/core';
/**
 * Generated class for the RequestissuePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-requestissue',
  templateUrl: 'requestissue.html',
})
export class RequestissuePage {

    pushPage:any;
    dataSet: any = [];
    recordCustomer:any=[];
    recordStore:any=[];



    constructor(public navCtrl: NavController, public navParams: NavParams,public translate: TranslateService)
      {
        this.recordCustomer = [
          {name:"Calvin Kelin"},
          {name:"Tommy Heighler"},
          {name:"Zara"}
        ];

        this.recordStore=[
          {name:"StoreA"},
          {name:"StoreB"},
          {name:"StoreC"}
        ];

        translate.use('en');
        translate.setTranslation('zh-tw',
        {
          "Trans Date":"",
          "To": "",
          "View Past Record": "",
          "Active Issue":'',
          QA: '',
          Store: '',
          Check: '',
          "Check Table": '',
          Customer: '',
          Name: '',
          Quantity: '',
          "Carton No": "",
          Style : '',
          "Style Rev": '',
          "Style Description": '',
          Buyer: '',
          Close: '',
          Confirm:'',
          Reset: '',
          Import: '',
          Delete: '',
          Query: '',
          "Record Table":"",
          "Trans Code": "",
          "Doc No": "",
          "Created By":"",
          "Status":""
        }
      );

        this.dataSet =[
          {
            type:"QA",
            store: "StoreA",
            trans: "2018-03-13",
            transCode: "435453",
            docNo: "e5345",
            creator: "sdfsdf",
            status: "Active",
            JO: "Jo342",
            cartons: "45",
            Qty: "45"
          },
          {
            type:"Active",
            store: "StoreA",
            trans: "2018-03-13",
            transCode: "435453",
            docNo: "e5345",
            creator: "sdfsdf",
            status: "Active",
            JO: "Jo342",
            cartons: "45",
            Qty: "45"
          }
        ];
      }

      changeLanguage(lang){
        this.translate.use(lang);
      }

      determinePushPage(item:any){
        if(item.type== 'QA' )
        {
          this.pushPage = QaissuePage;
        }

        else if(item.type == "Active"){
          this.pushPage = FnissuePage;
        }
        this.navCtrl.push(this.pushPage);
      }

      ionViewDidLoad() {
        console.log('ionViewDidLoad ViewrecordPage');
      }

    }



